<template>
  <div>
    <canvas
      ref="canvas"
      style="width:500px;height:500px;"
    ></canvas>
  </div>
</template>
<script>
export default {
  data () { return {} },
  mounted () { this.init() },
  methods: {
    init () {
      let totalWidth = 500
      let totalHeight = 250
      let colNumber = 10
      let rowNumber = 10
      let singleWidth = (totalWidth / colNumber) / 2
      let singleHeight = totalHeight / rowNumber
      let canvas = this.$refs.canvas
      let ctx = canvas.getContext('2d')
      ctx.fillStyle = '#E4E4E4'
      ctx.fillRect(0, 0, totalWidth, totalHeight)
      for (let i = 0, len = totalWidth / colNumber; i < len; i++) {
        let currentRow = Math.floor(i / rowNumber)
        let currentCol = i - (colNumber * currentRow)
        ctx.fillStyle = '#ffffff'
        ctx.fillRect(singleWidth * currentCol, singleHeight * currentRow, singleWidth, singleHeight)
        ctx.fillStyle = '#00ff00'
        ctx.fillText(i + 1, singleWidth * currentCol + singleWidth / 2, singleHeight * currentRow + singleHeight / 2)
      }
    }
  }
}
</script>



