<template>
  <el-input
    size="medium"
    class="search-input"
  >
    <el-button
      slot="append"
      icon="el-icon-search"
      @click="search()"
    ></el-button>
  </el-input>
</template>
<script>
export default {
  data () { return {} },
  methods: {
    search () { }
  }
}
</script>
<style>
.search-input {
  margin-left: 20%;
  width: 60%;
  margin-right: 20%;
}
</style>



