<template>
  <div>
    <el-card
      v-for="(item,index) in games"
      :key="index"
      shadow="hover"
      class="markedSite-container"
    >
      <router-link
        :to="{path:'/'+item.id}"
        target="_blank"
      >
        <div class="markedSite-ico">
          <img
            :src="item.iconUrl"
            onerror="this.src='./static/img/icon/link.png'"
            class="markedSite-ico"
          >
        </div>
        <div
          :title="item.name"
          class="markedSite-name"
        >{{item.name}}</div>
      </router-link>
    </el-card>
  </div>
</template>
<script>
import { queryGameList } from './../../assets/api/index'
export default {
  data () { return { games: [] } },
  created () {
    this.getGameList()
  },
  methods: {
    getGameList () {
      queryGameList().then(data => {
        this.games = data
      })
    }
  }
}
</script>
<style scoped>
.markedSite-container {
  width: 104px;
  height: 120px;
  margin: 10px;
  text-align: center;
  position: relative;
  overflow: visible;
  display: inline-block;
}
.markedSite-container:hover .markedSite-options {
  display: block;
}
.markedSite-ico {
  width: 64px;
  height: 64px;
}
.markedSite-name {
  width: 64px;
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.markedSite-options {
  display: none;
  position: absolute;
  top: -16px;
  right: -16px;
}
</style>



